/*Обнуление*/
@import "_zero.scss";



/* Шрифты */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital@0;1&display=swap');

:root {

}

html,
body {
	background-color: #fff;
	font-family: 'Roboto Condensed',Arial,sans-serif;
	overflow-x: hidden;
}



.logo {
	align-items: flex-start;
	width: 155px;
	height: 25px;
	margin-bottom: auto;
	margin-left: 3px;
}

/* ============== Стили для меню ============ */

.conteiner_nav {
	position: fixed;
	background-image: -webkit-gradient( linear, left top, left bottom, from(rgba(23,26,41,1)), to(rgba(23,26,41,1)) );
	font-family: 'Roboto Condensed',Arial,sans-serif;
	
	padding-left: 20px;
	.navbar-brand {

		font-size: 20px;
		font-weight: 900;

	}
	a {
		color: #fff;
	}
	
	align-items: center;

	
	
}
.nav_button {
	background-color: #fff;	


}
.nav-item .nav-link {
	color: #fff;

}
.navbar-light .navbar-brand {
	color: #fff;
}
.navbar-light .navbar-nav .active>.nav-link {
	color: #fff;
}
nav .phone {
	color: #fff;
}

nav .nav_icon {
	display: flex;
	flex: 0 0 40px;
	margin: 2.5px;
}
nav {

	justify-content: flex-end;
}
.container-xxl {
	width: 100%;
	max-width: 1140px;
	margin: 0 auto;
}

.expander {
	margin-left: auto;
}

.stil-t_2 {
	width: 180px;
	height: 50px;
	margin-bottom: 20px;
	margin-top: 20px;
}
.navbar-nav {
	overflow: auto;
}

.request_a_call {
	text-align: center;
	margin-top: 30px;
	margin-bottom: 10px;
	border: 2px solid #ff9900;
	width: 200px;
}
.icon:nth-child(1) {
	margin-left: auto;
}



/* ============== Стили для шапки ============ */

header {
	display: flex;
	text-align: center;
	flex-wrap: nowrap;
	padding: 120px 10px;
	.header_conteiner {
		align-items: center;

	}

	.carousel-indicators li img {

		border-radius: 10px;
	}
	.carousel-indicators li::before, .carousel-indicators li::after {

		display: none;
	}
	.carousel-indicators .active img {
		border: 4px solid #ff9900;
		border-radius: 10px;
	}

	.carousel-indicators {
		li {
			width: 50px;
		}
	}
	.carousel-indicators li {
		background-color: transparent;
	}

	.header_title {
		
		h1 {
			padding-left: 10px;
			font-size: 33px;
			font-weight: 700;
			justify-content: flex-start;
			margin-bottom: 30px;
		}
	}
}

.header_price {
	order: 3;
	flex: 1 1 1fr;
	margin-bottom: 50px;
	padding: 0 10px;

	h6 {
		color: rgb(73, 72, 72);
		font-size: 22px;
		margin: 10px 0;
	}

	.price_without_discount {
		text-decoration: line-through;
		color: rgb(106, 106, 106);
		padding: 10px;
		margin-bottom: 40px;
	}
	.discount_price {
		color: rgb(54, 54, 54);
		border-bottom: 4px solid #ff9900;
	}
	.discount_valid_until {
		font-size: 20px;
		font-weight: 700;
		color: rgb(54, 54, 54);
		margin: 20px 0;
		padding-bottom: 20px;
	}

	a {
		color: #fff;
	}
	.button_calculate_the_cost {

		background-color: #2a7fff;
		padding: 10px 5px;
		max-width: 350px;
		width: 100%;
		margin: 10px auto;
		
	}
	.button_call_the_measurer {
		
		background-color: #197d19;
		padding: 10px 5px;
		
		max-width: 350px;
		width: 100%;
		margin: 10px auto;
		

	}
}

.header_title {
	margin-top: 10px;
	order: 1;
	flex: 1 1 1fr;
}
.header_img {
	order: 2;
	position: relative;
	flex: 1 1 1fr;
	max-width: 90%;
	margin: 0 auto;
}

.order_more {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	background-color: #197d19;
	width: 180px;
	height: 30px;
	z-index: 9;
	font-size: 15px;
	height: 900;
	color: #fff;
	right: 10px;

	&:hover {
		text-decoration: none;
		color: #111;
	}
}




